export const defaultTheme = {
  "--theme-background-color": "rgba(255, 255, 255, 1)",
  "--theme-body-font": "system-ui",
  "--theme-error-color": "rgba(249, 97, 85, 1)",
  "--theme-primary-color": "rgba(0, 115, 202, 1)",
  "--theme-success-color": "rgba(74, 211, 172, 1)",
  "--theme-surface-color": "rgba(255, 255, 255, 1)",
  "--theme-text-color": "rgba(0, 0, 0, 0.7)",
  "--theme-title-font": "system-ui",
};

const themeVarMap = {
  bodyFont: "--theme-body-font",
  titleFont: "--theme-title-font",
  primaryColor: "--theme-primary-color",
  textColor: "--theme-text-color",
  successColor: "--theme-success-color",
  errorColor: "--theme-error-color",
  surfaceColor: "--theme-surface-color",
  backgroundColor: "--theme-background-color",
};

/**
 * Get the theme variables
 * @param {object} theme The theme object coming from CDP
 */
export const getThemeVariables = (theme) => {
  let themeProp;
  try {
    if (theme && Object.keys(theme).length) {
      themeProp = Object.entries(theme).reduce(
        (acc, [key, value]) =>
          value ? { ...acc, [`${themeVarMap[key]}`]: value } : acc,
        {}
      );
    }
    return themeProp;
  } catch (e) {
    console.error("Theme is not an object");
  }
};
