/* eslint-disable react/prop-types */
import React, { useRef, useEffect } from "react";
import { Editor, Transforms, Text } from "slate";
import { useSlate, ReactEditor } from "slate-react";
import { css } from "emotion";
import { Button, Icon, Menu, Portal } from "./helper";
import { Range } from "slate";

const toolBarPadding = 7; //necessary to pull out so overflow can be calculated without magic number

export const toggleFormat = (editor, format) => {
  const isActive = isFormatActive(editor, format);
  Transforms.setNodes(
    editor,
    { [format]: isActive ? null : true },
    { match: Text.isText, split: true }
  );
};

const isFormatActive = (editor, format) => {
  const [match] = Editor.nodes(editor, {
    match: (n) => n[format] === true,
    mode: "all",
  });
  return !!match;
};

const FormatButton = ({ format, icon }) => {
  const editor = useSlate();
  return (
    <Button
      reversed
      active={isFormatActive(editor, format)}
      onMouseDown={(event) => {
        event.preventDefault();
        toggleFormat(editor, format);
      }}
    >
      <Icon>{icon}</Icon>
    </Button>
  );
};

export const hoverLeaf = ({ attributes, children, leaf }) => {
  if (leaf.bold) {
    children = <strong>{children}</strong>;
  }

  if (leaf.italic) {
    children = <em>{children}</em>;
  }

  if (leaf.underlined) {
    children = <u>{children}</u>;
  }

  if (leaf.highlight) {
    children = <mark>{children}</mark>;
  }

  return <span {...attributes}>{children}</span>;
};

const toggleMark = (editor, format) => {
  const isActive = isMarkActive(editor, format);

  if (isActive) {
    Editor.removeMark(editor, format);
  } else {
    Editor.addMark(editor, format, true);
  }
};

const isMarkActive = (editor, format) => {
  const marks = Editor.marks(editor);
  return marks ? marks[format] === true : false;
};

export const hoverKeyboardHandler = (event, editor) => {
  if (event.ctrlKey) {
    switch (event.key) {
      case "b": {
        event.preventDefault();
        return toggleMark(editor, "bold");
      }
      case "i": {
        event.preventDefault();
        return toggleMark(editor, "italic");
      }
      case "u": {
        event.preventDefault();
        return toggleMark(editor, "underlined");
      }
      case "h": {
        event.preventDefault();
        return toggleMark(editor, "highlight");
      }
      default:
        break;
    }
  }
};

export const HoveringToolbar = () => {
  const ref = useRef();
  const editor = useSlate();

  useEffect(() => {
    const toolbarElement = ref.current;
    const { selection } = editor;

    if (!toolbarElement) {
      return;
    }

    if (
      !selection ||
      !ReactEditor.isFocused(editor) ||
      Range.isCollapsed(selection) ||
      Editor.string(editor, selection) === ""
    ) {
      toolbarElement.removeAttribute("style");
      return;
    }

    const selectedTextBounds = window
      .getSelection()
      .getRangeAt(0)
      .getBoundingClientRect();
    toolbarElement.style.opacity = 1;

    // verify toolbar doesn't go out of bounds on TOP
    if (selectedTextBounds.top - toolbarElement.offsetHeight <= 0) {
      toolbarElement.style.top =
        window.pageYOffset + toolbarElement.offsetHeight + 8 + "px";
    } else {
      toolbarElement.style.top = `${
        Math.floor(selectedTextBounds.top) +
        window.pageYOffset -
        toolbarElement.offsetHeight
      }px`;
    }
    // verify toolbar doesn't go out of bounds on RIGHT
    const toolbarWidth = 134; // pixels (84 + 2*padding)
    // 7*2px padding left+right
    toolbarElement.style.width = `${toolbarWidth - 2 * toolBarPadding}px`;
    if (selectedTextBounds.left + toolbarWidth > window.innerWidth) {
      const overflow =
        selectedTextBounds.left +
        toolbarWidth -
        window.innerWidth +
        toolBarPadding * 2; // pad L+R
      toolbarElement.style.left = `${
        window.pageXOffset + selectedTextBounds.left - overflow
      }px`;
    } else {
      toolbarElement.style.left = `${
        selectedTextBounds.left +
        window.pageXOffset -
        toolbarElement.offsetWidth / 2 +
        selectedTextBounds.width / 2
      }px`;
    }
  });

  return (
    <Portal>
      <Menu
        ref={ref}
        className={css`
          padding: 4px ${toolBarPadding}px 6px;
          position: absolute;
          z-index: 1;
          top: -10000px;
          left: -10000px;
          margin-top: -6px;
          opacity: 0;
          background-color: #222;
          border-radius: 4px;
          transition: opacity 0.75s;
        `}
      >
        <FormatButton format="highlight" icon="border_color" />
        <FormatButton format="bold" icon="format_bold" />
        <FormatButton format="italic" icon="format_italic" />
        <FormatButton format="underlined" icon="format_underlined" />
      </Menu>
    </Portal>
  );
};
