/* eslint-disable react/prop-types */
import React from "react";
import { defaultTheme } from "../utils/themeUtils";

const Layout = (props) => {
  const { activityTitle, activityLabel, instructions, captions } = props;
  const { bodyFont, titleFont } = props.theme;
  return (
    <div className="layout" style={{ fontFamily: bodyFont }}>
      {(activityTitle || activityLabel) && (
        <h4 style={{ fontFamily: titleFont }}>
          <span
            className="component-label"
            dangerouslySetInnerHTML={{ __html: activityLabel }}
          />
          {activityTitle && (
            <span
              className="component-title"
              dangerouslySetInnerHTML={{ __html: activityTitle }}
            />
          )}
        </h4>
      )}
      {instructions && (
        <div
          className="component-instructions"
          dangerouslySetInnerHTML={{ __html: instructions }}
        />
      )}
      <div className="component-container" style={defaultTheme}>
        {props.children}
      </div>
      {captions && (
        <div
          className="component-caption"
          dangerouslySetInnerHTML={{ __html: captions }}
        />
      )}
    </div>
  );
};

export default Layout;
