import React, { useEffect, useState } from "react";
import CodeBlock from "./CodeBlock";
import cdp from "@pearsontechnology/aero-plugin-api/lib/cdp";
import "./styles/main.scss";
import Layout from "./Layout";

const App = () => {
  const [cdpConfig, setCDPConfig] = useState(false);
  const [codeBlockEditable, setCodeBlockEditable] = useState(false);
  const [theme, setTheme] = useState({});

  useEffect(() => {
    const init = async () => {
      const cdpSubscribe = (config) => {
        setCDPConfig(config);
      };
      const cdpSubscribeTheme = (cdpTheme) => {
        setTheme(cdpTheme);
      };
      await cdp.register();
      Promise.all([
        cdp.context().get(),
        cdp.theme().get(),
        cdp.state().get(),
        cdp.state().subscribe(cdpSubscribe),
        cdp.theme().subscribe(cdpSubscribeTheme),
      ]).then((data) => {
        const [userType = "", cdpTheme = {}, cdpState = {}] = data || [];
        setCodeBlockEditable(userType === "EDITOR" ? true : false);
        setTheme(cdpTheme);
        setCDPConfig(cdpState);
      });
    };
    init();
  }, []);

  return (
    cdpConfig && (
      <Layout {...cdpConfig} theme={theme}>
        <CodeBlock
          config={cdpConfig}
          codeBlockEditable={codeBlockEditable}
          theme={theme}
        />
      </Layout>
    )
  );
};

export default App;
