/* eslint-disable react/prop-types */ /* turn off type checking for now */
import React from "react";
const Gutter = (props) => {
  const { totalLines, startLine = 1, showLines = true, bgColor } = props;

  const items = [];
  for (let i = 0; i < totalLines; i++) {
    items.push(
      <div className="gutterElement" key={i}>
        {i + startLine}
      </div>
    );
  }
  return (
    <div
      className="gutterContainer"
      aria-hidden="true"
      style={{ backgroundColor: bgColor }}
    >
      <pre>
        <code>{showLines && items}</code>
      </pre>
    </div>
  );
};

export default Gutter;
