/* eslint-disable react/prop-types */
import Prism from "prism-react-renderer/prism";
(typeof global !== "undefined" ? global : window).Prism = Prism;

require("prismjs/components/prism-matlab");
require("prismjs/components/prism-csharp");
require("prismjs/components/prism-aspnet");

import React, {
  useState,
  useMemo,
  useEffect,
  useCallback,
  Fragment,
} from "react";
import { Slate, Editable, withReact } from "slate-react";
import { createEditor } from "slate";
import { withHistory } from "slate-history";
import Gutter from "./components/Gutter/gutter";
import {
  HoveringToolbar,
  hoverLeaf,
  hoverKeyboardHandler,
} from "./components/Hover/toolbar";
import CDP from "@pearsontechnology/aero-plugin-api/lib/cdp";

import SyntaxHighLightingEditor from "react-simple-code-editor";
import Highlight, { defaultProps } from "prism-react-renderer";
import themeStyle from "../theme";
import { getThemeVariables } from "../utils/themeUtils";

const placeholderText = "Enter code here...";
const CodeBlockPlugin = (props) => {
  const editor = useMemo(() => withHistory(withReact(createEditor())), []);
  const {
    codeBlockText = initialValue,
    showLineNumber = "on",
    lineNumber = 1,
    language = "no language",
    coloring = "on",
    appearance = "light",
  } = props.config;

  const theme = props.theme;
  const themeProp = getThemeVariables(theme);

  const backgroundColor = appearance === "dark" ? "#F6F7F8" : "#fff";
  const startLine = parseInt(lineNumber);
  const showLines = showLineNumber === "on" ? true : false;

  useEffect(() => {
    if (coloring === "on") {
      setupPrismRenderingForAccessibility();
      Prism.languages.aspnet = Prism.languages.extend("csharp", {});
    }
  });

  // In order for Prism to correctly speak line numbers, we need
  // to reverse its order for aria-hidden.  The default "textarea"
  // cannot have the "Line 1" numbers injected hidden.
  const setupPrismRenderingForAccessibility = () => {
    const colorEditorEl = document.getElementById("colorEditor");
    if (colorEditorEl) {
      colorEditorEl.setAttribute("aria-hidden", "true");
      if (props.codeBlockEditable) {
        colorEditorEl.removeAttribute("readonly");
      } else {
        colorEditorEl.setAttribute("readonly", "true");
      }
      if (colorEditorEl.nextSibling) {
        colorEditorEl.nextSibling.setAttribute("aria-hidden", "false");
      }
    }
  };

  const [codeBlockContent, setCodeBlockContent] = useState(codeBlockText);
  const setCodeBlock = (codeBlockValue) => {
    setCodeBlockContent(codeBlockValue);
    CDP.state("codeBlockText").set(
      convertCodeBlockContentToObject(codeBlockValue)
    );
  };

  const handleKeyboardEvent = (event) => {
    if (!event) return;

    if (coloring === "off") {
      hoverKeyboardHandler(event, editor);
    }

    if (event.keyCode === 9 && event.shiftKey) {
      event.preventDefault();
      editor.deleteBackward(" ");
    } else if (event.keyCode === 9) {
      event.preventDefault();
      editor.insertText("  ");
    }
  };

  /* ideally this could be part of the renderElement */
  const renderSlateScreenReaderLineNumbers = () => {
    if (showLineNumber === "on") {
      const els = document.getElementsByClassName("screen-reader-only");
      for (let n = 0; n < els.length; n++) {
        els[n].innerHTML = "Line " + (n + startLine);
      }
    }
  };

  const renderElement = useCallback(({ attributes, children }) => {
    return (
      <span alt="code line" className="code-content">
        {/* ideally {"Line" + currentLineNumber} */}
        <div
          style={{ userSelect: "none" }}
          contentEditable={false}
          className="screen-reader-only"
        ></div>
        <div {...attributes}>{children}</div>
      </span>
    );
  }, []);

  const renderEditor = () => {
    if (coloring === "on") {
      return (
        <SyntaxHighLightingEditor
          value={convertCodeBlockContentToString(codeBlockContent)}
          onValueChange={setCodeBlock}
          highlight={highlight}
          placeholder={placeholderText}
          textareaId="colorEditor"
        />
      );
    }
    return (
      <Slate
        editor={editor}
        value={convertCodeBlockContentToObject(codeBlockContent)}
        onChange={(value) => setCodeBlock(value)}
      >
        <HoveringToolbar />
        <Editable
          readOnly={!props.codeBlockEditable}
          onKeyDown={(event) => handleKeyboardEvent(event)}
          placeholder={placeholderText}
          style={{ whiteSpace: "inherit" }}
          renderLeaf={hoverLeaf}
          renderElement={renderElement}
        />
      </Slate>
    );
  };

  const highlight = (code) => (
    <Highlight
      {...defaultProps}
      code={code}
      language={language}
      theme={themeStyle}
    >
      {({ tokens, getLineProps, getTokenProps }) => (
        <Fragment>
          {tokens.map((line, i) => (
            <div key={`line-${i}`} {...getLineProps({ line, key: i })}>
              {showLineNumber === "on" && (
                <div className="screen-reader-only">Line {i + startLine}</div>
              )}
              {line.map((token, key) => (
                <span
                  key={`line-token-${i}`}
                  {...getTokenProps({ token, key })}
                />
              ))}
            </div>
          ))}
        </Fragment>
      )}
    </Highlight>
  );
  return (
    <div className="codeEditorContainer" style={themeProp}>
      <style>
        {theme && theme.cssUrl ? `@import url("${theme.cssUrl}");` : ""}
        {theme && theme.css ? theme.css : ""}
      </style>
      <Gutter
        totalLines={codeBlockText.length || 1}
        startLine={startLine}
        showLines={showLines}
        bgColor={backgroundColor}
      />
      <div className="codeEditor" style={{ backgroundColor: backgroundColor }}>
        <pre>
          <code>{renderEditor()}</code>
          {coloring === "off" && renderSlateScreenReaderLineNumbers()}
        </pre>
      </div>
    </div>
  );
};

const convertCodeBlockContentToString = (codeblockContent) => {
  if (typeof codeblockContent === "string") return codeblockContent;
  let codeBlockContentStr = "";
  if (codeblockContent) {
    for (let i = 0; i < codeblockContent.length; i++) {
      if (codeblockContent[i]) {
        let codeFontStr = "";
        const codeFontArr = codeblockContent[i].children || [];
        if (codeFontArr.length > 1) {
          for (let j = 0; j < codeFontArr.length; j++) {
            codeFontStr += codeFontArr[j].text;
          }
          codeBlockContentStr +=
            codeFontStr + (i < codeblockContent.length - 1 ? "\n" : "");
        } else {
          codeBlockContentStr +=
            codeFontArr[0].text + (i < codeblockContent.length - 1 ? "\n" : "");
        }
      }
    }
  }
  return codeBlockContentStr;
};

const convertCodeBlockContentToObject = (codeblockContent) => {
  if (typeof codeblockContent === "object") return codeblockContent;
  const codeBlockContentArr = codeblockContent.split("\n") || [];
  const codeBlockResultArr = [];
  if (codeBlockContentArr.length) {
    for (let i = 0; i < codeBlockContentArr.length; i++) {
      codeBlockResultArr.push({
        children: [
          {
            text: codeBlockContentArr[i],
          },
        ],
      });
    }
  } else {
    codeBlockResultArr.push(initialValue);
  }
  return codeBlockResultArr;
};

const initialValue = [
  {
    children: [{ text: "" }],
  },
];

export default CodeBlockPlugin;
